import React, { useState, useContext } from 'react';
import { ReferenceDataContext } from "../../context/context";
import Button from 'react-bootstrap/Button';
import Modal from 'react-bootstrap/Modal';

import './login.css';

function ModalLogin() { 
    const [login, setLogin] = useState({
        usuario: '',
        password: ''
    });

    var setUsuarioValue =(value) => {
        var newLogin = {...login, usuario : value };
        setLogin(newLogin);       
    };

    var setPasswordValue =(value) => {       
        var newLogin = {...login, password : value };
        setLogin(newLogin); 
    };

    const { setUser } = useContext(ReferenceDataContext)
    const [show, setShow] = useState(false);
  
    const handleClose = () => setShow(false);
    const handleShow = () => setShow(true);
    const handleLogin = () => {
        var newUser = {
            nome: login.usuario,
            personagem: {
                nome: '',
                classe: ''
            }
        };       

        setUser(newUser);
        setShow(false);
    };
    
    return (
      <>
        <Button variant="primary" onClick={handleShow}>
          Login
        </Button>
  
        <Modal show={show} onHide={handleClose}>
          <Modal.Header closeButton>
            <Modal.Title>Informe seu usuário e senha</Modal.Title>
          </Modal.Header>
          <Modal.Body>
 
            <div>
                 <div className="row">
                     <div className="col-4">
                       Usuario:
                     </div>
                     <div className="col-6">
                       <input type="text" value={login.usuario} onChange={ e => setUsuarioValue(e.target.value) }  ></input>
                     </div>
                 </div>
                 <div className="row">
                     <div className="col-4">
                       Senha:
                     </div>
                     <div className="col-6">
                       <input type="password" value={login.password} onChange={ e => setPasswordValue(e.target.value) }></input>
                     </div>
                 </div>
             </div>

          </Modal.Body>
          <Modal.Footer>
            <Button variant="secondary" onClick={handleClose}>
              Fechar
            </Button>
            <Button variant="primary" onClick={handleLogin}>
              Logar
            </Button>
          </Modal.Footer>
        </Modal>
      </>
    );
  }
  const Login = () => (
    <ModalLogin>
    </ModalLogin>    
  );

export default Login;

