import React from "react";
import './perk.css';

function Perk(props) {
    const [perk, setPerk] = React.useState(props.perk);

    var handleClick = () => {
        console.log('perk', perk);
    };

    var classNamePerk = "Perk-Btn";

    var perkButton = (perk.show ?
        <div className={classNamePerk} onClick={handleClick}>
            <img alt={perk.description} src={perk.icon}></img>
        </div>
        :
        <div></div>)

    return (perkButton);
  }
  
  export default Perk;