import React, { useState, useEffect } from 'react';
import Perk from '../perk/perk';
import './content.css';

function Content() {
  const [astuciaPerks, setAstuciaPerks] = useState(null);
  const [saudePerks, setSaudePerks] = useState(null);
  const [bravuraPerks, setBravuraPerks] = useState(null);

  useEffect(() => {

    fetch('ressource/astucia-perks.json')
      .then(res => res.json())
      .then(json => {
        setAstuciaPerks(json);
      });

    fetch('ressource/saude-perks.json')
      .then(res => res.json())
      .then(json => {
        setSaudePerks(json);
      });

    fetch('ressource/bravura-perks.json')
      .then(res => res.json())
      .then(json => {
        setBravuraPerks(json);
      });

  }, []);

  let astucia = (<div className="App-Content container No-Perks">
    <div className="row">
      <div className="col-12"> Sem vantagens no momento</div>
    </div>
  </div>);

  var saude = (<div className="App-Content container No-Perks">
    <div className="row">
      <div className="col-12"> Sem vantagens no momento </div>
    </div>
  </div>);

  var bravura = (<div className="App-Content container No-Perks">
    <div className="row">
      <div className="col-12"> Sem vantagens no momento </div>
    </div>
  </div>);

  if (astuciaPerks) {

    let keys = Object.keys(astuciaPerks);

    astucia = (<div className="col">
      <h2>Astucia</h2>
      <div className="App-Content container" style={{ backgroundImage: "url(/astucia-bg.jpg)" }}>
        {
          keys.map(key =>
            <div className="row Perk-Linha" key={key}>
              {
                astuciaPerks[key].map((perk) =>
                  <div key={perk.id} className="col-3">
                    <Perk perk={perk}></Perk>
                  </div>)
              }
            </div>
          )
        }
      </div>
    </div>);
  }

  if (saudePerks) {

    let keys = Object.keys(saudePerks);
    saude = (<div className="col">
      <h2>Saúde</h2>
      <div className="App-Content container" style={{ backgroundImage: "url(/saude-bg.jpg)" }}>
        {
          keys.map(key =>
            <div className="row Perk-Linha" key={key}>
              {
                saudePerks[key].map((perk) =>
                  <div key={perk.id} className="col-3">
                    <Perk perk={perk}></Perk>
                  </div>)
              }
            </div>
          )
        }
      </div>
    </div>);
  }

  if (bravuraPerks) {

    let keys = Object.keys(bravuraPerks);
    bravura = (<div className="col">
      <h2>Bravura</h2>
      <div className="App-Content container" style={{ backgroundImage: "url(/bravura-bg.jpg)" }}>
        {
          keys.map(key =>
            <div className="row Perk-Linha" key={key}>
              {
                bravuraPerks[key].map((perk) =>
                  <div key={perk.id} className="col-3">
                    <Perk perk={perk}></Perk>
                  </div>)
              }
            </div>
          )
        }
      </div>
    </div>);
  }

  return (
    <div className="flex-container">
      <div className="row">

        <div class="col-1">
          <h2>Nível</h2>
          <div className="Block-Perks-Level">
            <div className="row Perk-Linha bg-primary">
              <div className="Perks-Level">
                - 1 -
              </div>
            </div>
            <div className="row Perk-Linha bg-success">
              <div className="Perks-Level">
                - 3 -
              </div>
            </div>
            <div className="row Perk-Linha bg-secondary">
              <div className="Perks-Level">
                - 6 -
              </div>
            </div>
          </div>
        </div>

        {astucia}
        {saude}
        {bravura}
      </div >
    </div >
  );
}

export default Content;
