import React, { useContext } from 'react';
import { ReferenceDataContext, UserEmpty } from "../../context/context";
import Button from 'react-bootstrap/Button';
import Login from '../login/login';
import './header.css';

function Header() {
  const title = 'Skill Tree 5e Planinautas';
  const { user, setUser } = useContext(ReferenceDataContext);

  const handleLogout = () => {        
      setUser(UserEmpty);  
  };

  var userData = user.nome !== '' ?
   (
   <div className="User-Data"> 
   <span> Usuário: {user.nome} </span>
   <Button variant="primary" onClick={handleLogout}>
          Deslogar
        </Button>
   </div>
   ) 
  : 
  (<Login/>);
  var charData = user.personagem.nome !== '' ?
  (<div className="Char-Data"> Personagem: <span>{user.personagem.nome}</span> | Classe: <span>{user.personagem.classe}</span> </div>) 
  :
  (<div></div>);

  return (   
      <React.Fragment>
        <header className="Header">
            <div className="Page-Title">{title}</div>
            {userData}
        </header>
        {charData}
    </React.Fragment> 
  );
}

export default Header;
