import { createContext, useState } from "react";

var ReferenceDataContext = createContext({});
var UserEmpty = {
  nome: '',
  personagem: {
      nome: '',
      classe: '',
      perks: [
        {
          id: null,
          level: 1
        },
        {
          id: null,
          level: 3
        }       
      ]
  }
};

const ReferenceDataContextProvider = ({ children }) => {
  const [user, setUser] = useState(UserEmpty);    

  return (
    <ReferenceDataContext.Provider value={{ user, setUser }}>
      {children}
    </ReferenceDataContext.Provider>
  );
};

export { ReferenceDataContext, ReferenceDataContextProvider, UserEmpty };